section, footer, nav {
  display: block;
}

body {
  -webkit-text-size-adjust: none;
  font-size: 14px;
  padding: 3em;
}

.card {
  width:100%;
  height: 350px;
  margin-top: 20px;
  position: relative;
  float: left;
  background-repeat: no-repeat, repeat;
  background-size: cover;
  background-position: center;
  border-radius: 15px;

  &:after{
    content:"";
    background:rgba(0,0,0,.4);
    width:100%;
    height:100%;
    position:absolute;
    left:0;
    top:0;
    z-index: 1;
    border-radius: 15px;
  }

  .card__front, .card__button {
    display: block;
    top: 0; bottom: 0;
    left: 0; right: 0;
    width: 100%;
    height: inherit;
    position: absolute;
    overflow: hidden;
    text-align: center;   
  }

  p {
    padding: 0 20px;    
  }

  .card__front {
    width: 100%;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 8.0em;
    background: transparent;
    transition: all .5s ease-in-out;
    z-index: 2;

    .card__sub {
      font-weight: 400;
      font-size: 14px;	
      letter-spacing: 2.33px;	
      line-height: 19px;
      margin: 5px;
    }

    .card__title {
      font-size: 28px;	
      font-weight: bold;	
      line-height: 32px;	
      text-align: center;
      margin: 5px;
    }

    .card__info {      
      margin-top: 400px;
    }        
  }
  .card__button {
    height: 100px;
    margin-top: 250px;
    padding-top: 25px;
    z-index: 2;

    a {
      opacity: 0;
      transition: all .5s ease-in-out;
    }
  }

  &:hover {

    .card__button {
      a {
        opacity: 1; 
      }
    }        
    .card__front {
      top: -80px;
      .card__info {        
        margin-top: 20px;
      }
    }
  }
}

@media (max-width: 768px) {
    .card {
        height: 280px !important;

        .card__button {
            margin-top: 200px;
        }
    } 
}