.header {
    height: 520px;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 15px;
    position: relative;
    background-repeat: no-repeat, repeat;
    background-size: cover;
    background-position: center;    	
    text-align: center;

    &:after{
        content:"";
        background:rgba(0,0,0,.4);
        width:100%;
        height:100%;
        position:absolute;
        left:0;
        top:0;
        z-index: 1;
        border-radius: 15px;
    }
    
    .header__front {
        display: block;
        top: 0; bottom: 0;
        left: 0; right: 0;
        width: 100%;
        height: inherit;
        position: absolute;
        overflow: hidden;   
        padding-top: 20%;
        width: 60%;
        margin-left: auto;
        margin-right: auto;
        z-index: 2;
      }

    .header__sub {
        text-align: center;
        font-weight: 400;
        margin-bottom: 5px;
        font-size: 14px;	
        letter-spacing: 2.33px;	
        line-height: 19px;
    }
    .header__info {
        font-size: 16px;
        line-height: 26px;
        text-align: center;
    }
}

@media (max-width: 768px) {
    .header {
        height: 280px;

        .header__logo {
            width: 150px;
        }
        .header__info {
            font-size: 14px;
        }
    }
}